import { requestPost, requestGet } from '@/api/api.js'
import { Toast } from 'mint-ui'
import { getCode } from '@/api/common.js'
export default {
    data() {
        return {
            id: '9',
            addr: '',
            remark: '',
            coinType: 'USDT',
            vcode: '',
            default: "0",
            show: true,
            popupVisible: false,
            coinList: [],
            timer: null,
            count: ''
        };
    },
    components: {},
    computed: {},
    created() {
        this.getCoin()
    },
    methods: {
        confirmSave() {
            this.addWallet()
        },
        cancel(){
        this.$router.go(-1)
        },
        addWallet() {
            requestPost('/api/v1/user_wallet', {
                wallet_id: this.id,
                addr: this.addr,
                code: this.vcode,
                remark: this.remark,
                default: this.default,
            }).then(res => {
                if (res.data.code == 0) {
                    Toast({
                        message: "添加成功",
                        position: "top",
                        duration: 2000
                    });
                    this.$router.go(-1)
                } else if (res.data.code == 1) {
                    Toast({
                        message: res.data.msg,
                        position: "top",
                        duration: 2000
                    });
                }

            })
        },

        showCoin() {
            this.popupVisible = true
        },
        selectCoin(coin) {
             
            this.coinType = coin.name
            this.id = coin.id
            this.popupVisible = false
        },
        getCoin() {
            requestGet('/api/v1/wallet/withdraw_wallet').then(res => {
                if (res.data.code == 0) {
                    this.coinList = res.data.data
                    
                }
            })
        },
        showWalletLlist() {
            this.popupVisible = true


        },
        getCode() {
            
            this.show = false;
            const TIME_COUNT = 60;
            if (!this.timer) {
                this.count = TIME_COUNT;
                this.sendCode()
                this.show = false;

                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= TIME_COUNT) {
                        this.count--;
                    } else {
                        this.show = true;
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                }, 1000);
            }
        },

        sendCode() {
            requestPost("/api/v1/user/send_code", {
                value: JSON.parse(localStorage.getItem('user_info')).mobile,
                type: "1",
                mobile_prefix: "86"
            }).then(res => {


                if ((res.data.status == "fail")) {
                    Toast({
                        message: res.data.msg,
                        position: "top",
                        duration: 2000
                    });
                } else if ((res.data.status == "success")) {
                    Toast({
                        message: "发送成功",
                        position: "top",
                        duration: 2000
                    });
                }
            });
        },
    }
}



