import { requestPost, requestGet } from "@/api/api.js";
import { setToken, getToken, removeToken } from "@/utils/auth.js";
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      backgroundDiv: {
        backgroundImage: "url(" + require("@/assets/bg.png") + ")"
      },
      popupVisible: false,
      country: "",
      mobilePrefix: "86",
      username: "",
      password: "",
      slots: [
        {
          flex: 1,
          values: [],
          className: "slot1",
          textAlign: "center",
          defaultIndex: 1
        }
      ],
      paramData: null
    };
  },
  methods: {
    toMobileLogin() {
      this.$router.push({ path: "/mobileLogin" });
    },
    selectPrefix() {
      this.popupVisible = true;
      this.getcountry();
    },
    getcountry() {
      requestGet("/api/v1/mobile_prefix").then(res => {
        if (res.data.status == "success") {
          let arr = [];
          res.data.data.forEach(el => {
            arr.push({
              cn_name: el.cn_name + "(" + el.mobile_prefix + ")",
              id: el.id,
              mobile_prefix: el.mobile_prefix
            });
          });
          this.slots[0].values = arr;
        }
      });
    },
    onDataChange(picker, values) {
      if (values[0]) {
        this.country = values[0].cn_name;
        this.mobilePrefix = values[0].mobile_prefix;
      }
    },
    close() {
      this.popupVisible = false;
    },

    toLogin() {
      let data = {
        email: this.username,
        password: this.password
      };

      requestPost("/api/v1/user/email_login", data).then(res => {
        if (res.data.status == "fail") {
          Toast({
            message: res.data.msg,
            position: "top",
            duration: 2000
          });
        } else if (res.data.status === "success") {
          Toast({
            message: "登录成功",
            position: "top",
            duration: 2000
          });
          localStorage.setItem("user_info", JSON.stringify(res.data.data));

          setToken(res.data.data.token);

          this.$router.push({ path: "/", query: { status: "login" } });
        }
      });
    },
    forgetPassword() {
      this.$router.push({ name: "forgetPassword",params:{from:'email'} });
    },
    toRegister() {
      this.$router.push({ path: "/register" });
    }
  }
};



