import { requestPost, requestGet } from "@/api/api.js";
// import { hex_sha1 } from "@/utils/sha1.js";
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      popupVisible: false,
      mobilePrefix: "86",
      ruleForm: {
        phone: "",
        codes: "",
        invite_id: "",
        pwd: "",
        checkpwd: "",
        type: "1"
      },
      phone: "",
      username: "",
      password: "",
      number: "",
      pwd: "password",
      show: true,
      timer: null,
      count: "",
      slots: [
        {
          flex: 1,
          values: [],
          className: "slot1",
          textAlign: "center",
          defaultIndex: 1
        }
      ]
    };
  },

  components: {},

  computed: {},

  methods: {
    onDataChange(picker, values) {
      if (values[0]) {
        this.country = values[0].cn_name;
        this.mobilePrefix = values[0].mobile_prefix;
      }
    },
    selectPrefix() {
      this.popupVisible = true;

      this.getcountry();
    },
    getcountry() {
      requestGet("/api/v1/mobile_prefix").then(res => {
        if (res.data.status == "success") {
          let arr = [];
          res.data.data.forEach(el => {
            arr.push({
              cn_name: el.cn_name + "(" + el.mobile_prefix + ")",
              id: el.id,
              mobile_prefix: el.mobile_prefix
            });
          });
          this.slots[0].values = arr;
        }
      });
    },
    register() {
      requestPost("/api/v1/user", {
        mobile_prefix: this.mobilePrefix,
        type: "1",
        mobile: this.ruleForm.phone,
        password: this.ruleForm.pwd,
        invite_id: this.ruleForm.invite_id,
        code: this.ruleForm.codes
      }).then(res => {
        if (res.data.status == "success") {
          Toast({
            message: res.data.msg,
            position: "top",
            duration: 2000
          });
          this.$router.push({ path: "/mobileLogin" });
        } else {
          Toast({
            message: res.data.msg,
            position: "top",
            duration: 2000
          });
        }
      });
    },
    show1() {
      if (this.pwd == "password") {
        this.pwd = "text";
      } else {
        this.pwd = "password";
      }
    },
    getCode() {
      this.show = false;
      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.sendCode();
        this.show = false;

        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000);
      }
    },

    sendCode() {
      requestPost("/api/v1/user/send_code", {
        value: this.ruleForm.phone,
        type: "1",
        mobile_prefix: "86"
      }).then(res => {
        // console.log(res);

        if (res.data.status == "fail") {
          Toast({
            message: res.data.msg,
            position: "top",
            duration: 2000
          });
        } else if (res.data.status == "success") {
          Toast({
            message: "发送成功",
            position: "top",
            duration: 2000
          });
        }
      });
    },
    toEmail() {
      this.$router.push("/emailRegister");
    },
    close() {
      this.popupVisible = false;
    }
  }
};



