import { tips, smgs } from "@/api/common.js";
import { MessageBox } from "mint-ui";
import { requestPost, requestGet, requestPut } from "@/api/api.js";
import QRCode from "qrcodejs2";
import "@/css/widthdrow.less";
import { num } from "@/utils/validate/rules1.js";
export default {
  data() {
    return {
      popupVisible: false,
      number: "",
      available: "",
      withdraw_money: "",
      // editAbled:false,
      code: "",
      fund_asset: "",
      wallets: {
        name: "",
        addr: ""
      },
      walletList: []
    };
  },
  created() {
    this.type = this.$route.params.type;
    //  this.fund_asset=this.$route.params.fund_asset
    this.setEditAbled()
    this.getDefaultWallet();
    this.getAvailable();
    console.log("data:", this.$route.params);
  },
  components: {},
  computed: {},
  methods: {
    setEditAbled(){
    
      if(this.$route.params.asset_type==0){
       
        //  this.editAbled==true
         
      }else if(this.$route.params.asset_type==1){
        // this.editAbled==false
      }
    },
    validateWithDraw() {
      if (this.available == 0) {
        tips("提现金额为0，不可提现！");
      } else if (this.withdraw_money == "" || !num(this.withdraw_money)) {
        tips("请输入正确的提现金额！");
      } else if (this.withdraw_money > this.available) {
        tips("提现金额不得大于可提现金额");
      }else {
        this.withdraw();
      }
    },
    withdraw() {
      let _this = this;
      requestPost("/api/v1/withdraw", {
        asset_type: this.$route.params.asset_type,
        wallet_name: this.wallets.name,
        wallet_addr: this.wallets.addr,
        money: this.withdraw_money,
        code: this.code
      }).then(res => {
        if (res.data.code == "0") {
          // tips("提现成功");
          // this.$router.push({path:'/',query:{"selected":'mine'}})
          this.tips(_this, res.data.data.money, res.data.data.wallet_addr);
        } else if (res.data.code == "1") {
          tips(res.data.msg);
        }
      });
    },
    getWalletList() {
      requestGet("/api/v1/user_wallet").then(res => {
        if (res.data.code == 0) {
          this.walletList = res.data.data;
          this.popupVisible = true;
        }
      });
    },
    getDefaultWallet() {
      requestGet("/api/v1/wallet/get_default_wallet").then(res => {
        if (res.data.status == "success") {
          this.wallets = res.data.data;
          this.$nextTick(function() {
            this.qrcode(this.wallets.addr);
          });
        } else {
          MessageBox.alert("还没有设置钱包信息！请去设置钱包").then(res => {
            this.$router.push({ path: "/walletMange" });
          });
        }
      });
    },
    qrcode(wallet_addr) {
      let qrcode = new QRCode("qrcode", { text: wallet_addr }); // 二维码内容
    },
    setDefault(addr, id, setData) {
      requestPut("/api/v1/user_wallet/" + id, setData).then(res => {
        if (res.data.code == 0) {
          this.wallets.addr = addr;
          this.$nextTick(function() {
            document.getElementById("qrcode").innerHTML = "";
            this.qrcode(this.wallets.addr);
          });
          this.popupVisible = false;
        } else {
          tips(res.data.msg);
        }
      });
    },
    getAvailable() {
      requestGet("/api/v1/asset").then(res => {
        if (res.data.code == 0) {
          // alert(this.$route.params.asset_type)
          if (this.$route.params.asset_type == 0) {
            this.available = res.data.data.fund_asset;
            this.withdraw_money=res.data.data.fund_asset
          } else if (this.$route.params.asset_type == 1) {
            this.available = res.data.data.available;
          }
        }
      });
    },
    tips(_this, money, wallet_addr) {
      let msg = `
      <div  class="">
        <span>提现金额:</span> <span>${money}</span>
         </div>
         <div>
           <span>提现钱包地址:</span> <span>${wallet_addr}</span>
         </div>
         <div>
           <span>提现状态:</span> <span>人工正在审核，请稍后查询</span>
         </div>
     </div>
    `;
      MessageBox({
        title: "提示",
        message: msg
      }).then(function() {
        console.log(123);
        // _this.$router.push({ path: "/mine" });
        _this.$router.push({ path: "/", query: { selected: "mine" } });
      });
    },
    all() {
      this.withdraw_money = this.available;
    }
  }
};



