import { requestPost } from "@/api/api.js";
// import { hex_sha1 } from "@/utils/sha1.js";
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      
      ruleForm: {
        email: "",
        codes: "",
        invite_id: "",
        pwd: "",
        checkpwd: "",
        type: "1"
      },
      email: "",
      username: "",
      password: "",
      number: "",
      pwd: "password",
      show:true,    
      timer:null,
      count:''
    };
  },
  methods: {
    register() {
      requestPost("/api/v1/user", {
        type: "2",
        email: this.ruleForm.email,
        password: this.ruleForm.pwd,
        invite_id: this.ruleForm.invite_id,
        code: this.ruleForm.codes
      }).then(res => {
        if (res.data.status == "success") {
           Toast({
            message: res.data.msg,
            position: "top",
            duration: 2000
          });
          this.$router.push({ path: "/EmailLogin" });
        } else {
          Toast({
            message: res.data.msg,
            position: "top",
            duration: 2000
          });
        }
      });
    },
    show1() {
      if (this.pwd == "password") {
        this.pwd = "text";
      } else {
        this.pwd = "password";
      }
    },
    getCode() {
      this.show = false;
      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.sendCode()
        this.show = false;

        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000);
      }
    },

    sendCode() {
      requestPost("/api/v1/user/send_code", {
        value: this.ruleForm.email,
        type: "2",
        
      }).then(res => {
          if ((res.data.status == "fail")) {
          Toast({
            message: res.data.msg,
            position: "top",
            duration: 2000
          });
        } else if ((res.data.status == "success")) {
          Toast({
            message: "发送成功",
            position: "top",
            duration: 2000
          });
        }
      });
    }
  }
};



